@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&family=Sarabun:wght@400;700&display=swap');


* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;

	--black-color: #000;
	--white-color: #fff;

	--hover-time: .4s;
}

ul,
li {
	list-style: none;
}

body {
	font-family: "Inter", sans-serif
}

.container {
	max-width: 1430px;

	padding: 0 15px;
	margin: 0 auto;
}